@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quattrocento+Sans);
body {
	margin: 0;

	color: #FFF;

	color: var(--white);

	background: #1E1F25;

	font-family:
		-apple-system,
		BlinkMacSystemFont,
		'Montserrat',
		'Segoe UI',
		'Roboto',
		'Oxygen',
		'Ubuntu',
		'Cantarell',
		'Fira Sans',
		'Droid Sans',
		'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

:root {
	--white: #FFF;
}

ul.nav {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	box-sizing: content-box;
	width: 100%;
	max-width: 1000px;
	height: 50px;
	margin: auto;
	padding: 25px 0;

	list-style-type: none;

	text-align: center;
}

ul.nav li {
	min-width: 80px;
	margin: auto;
	padding: 1rem 2rem 1.15rem;

	cursor: pointer;
	text-transform: uppercase;

	color: #EBEBEB;
}

ul.nav li a {
	text-decoration: none;

	color: #EBEBEB;
}

ul.nav li:hover {
	-webkit-animation: spring 300ms ease-out;
	        animation: spring 300ms ease-out;

	color: #27262C;
	background-image: url('https://scottyzen.sirv.com/Images/v/button.png');
	background-size: 100% 100%;
	text-shadow: 0 -1px 0 #EF816C;

	font-weight: bold;
}

ul.nav li:active {
	-webkit-transform: translateY(4px);
	        transform: translateY(4px);
}

@-webkit-keyframes spring {
	15% {
		-webkit-transform: scale(1.2, 1.1);
		-webkit-transform-origin: center center;
	}

	40% {
		-webkit-transform: scale(0.95, 0.95);
		-webkit-transform-origin: center center;
	}

	75% {
		-webkit-transform: scale(1.05, 1);
		-webkit-transform-origin: center center;
	}

	100% {
		-webkit-transform: scale(1, 1);
		-webkit-transform-origin: center center;
	}
}

@keyframes spring {
	15% {
		-webkit-transform: scale(1.2, 1.1);
		-webkit-transform-origin: center center;
	}

	40% {
		-webkit-transform: scale(0.95, 0.95);
		-webkit-transform-origin: center center;
	}

	75% {
		-webkit-transform: scale(1.05, 1);
		-webkit-transform-origin: center center;
	}

	100% {
		-webkit-transform: scale(1, 1);
		-webkit-transform-origin: center center;
	}
}

.page-footer {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-items: center;

	align-items: center;

	height: 40px;
	margin-top: 40px;

	color: var(--white);

	background-color: dimgray;
	box-shadow: 0 0 10px rgba(216 216 216 / 20%);
}

.page-footer a {
	color: var(--white);
}

.movies {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
	grid-gap: 1rem;
	gap: 1rem;
}

.movies a {
	text-decoration: none;
}

.nothing {
	color: var(--white);
}

.movie {
	display: flex;
	flex-direction: column;

	width: 100%;

	transition: all 0.3s;

	border-radius: 5px;
	background-color: #333;
	box-shadow: none;
}

.movies img {
	flex-grow: 1;

	width: 100%;

	border-radius: 5px 5px 0 0;
}

.movie:hover {
	-webkit-transform: scale(1.02);
	        transform: scale(1.02);

	box-shadow: 0 0 10px rgba(220, 223, 6, 1);
}

.movie img {
	height: 380px;
}

.card.movie {
	height: 100%;
}

.card-title {
	color: var(--white);

	font-size: 22px;
	font-weight: 500;
	line-height: 24px;
}

.card .card-content {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	justify-content: space-between;

	padding: 10px 10px 10px 15px;
}

.card-content p {
	color: yellow;
}

.loading {
	position: fixed;
	z-index: 9999;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	pointer-events: none;
}

.loading-text {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;

	width: 100%;
	height: 100px;
	margin: auto;

	text-align: center;

	line-height: 100px;
}

.loading-text span {
	display: inline-block;

	margin: 0 5px;

	color: #FFF;

	font-family: "Quattrocento Sans", sans-serif;
}

.loading-text span:nth-child(1) {
	-webkit-filter: blur(0);
	        filter: blur(0);

	-webkit-animation: blur-text 1.5s 0s infinite linear alternate;
	animation: blur-text 1.5s 0s infinite linear alternate;
}

.loading-text span:nth-child(2) {
	-webkit-filter: blur(0);
	        filter: blur(0);

	-webkit-animation: blur-text 1.5s 0.2s infinite linear alternate;
	animation: blur-text 1.5s 0.2s infinite linear alternate;
}

.loading-text span:nth-child(3) {
	-webkit-filter: blur(0);
	        filter: blur(0);

	-webkit-animation: blur-text 1.5s 0.4s infinite linear alternate;
	animation: blur-text 1.5s 0.4s infinite linear alternate;
}

.loading-text span:nth-child(4) {
	-webkit-filter: blur(0);
	        filter: blur(0);

	-webkit-animation: blur-text 1.5s 0.6s infinite linear alternate;
	animation: blur-text 1.5s 0.6s infinite linear alternate;
}

.loading-text span:nth-child(5) {
	-webkit-filter: blur(0);
	        filter: blur(0);

	-webkit-animation: blur-text 1.5s 0.8s infinite linear alternate;
	animation: blur-text 1.5s 0.8s infinite linear alternate;
}

.loading-text span:nth-child(6) {
	-webkit-filter: blur(0);
	        filter: blur(0);

	-webkit-animation: blur-text 1.5s 1s infinite linear alternate;
	animation: blur-text 1.5s 1s infinite linear alternate;
}

.loading-text span:nth-child(7) {
	-webkit-filter: blur(0);
	        filter: blur(0);

	-webkit-animation: blur-text 1.5s 1.2s infinite linear alternate;
	animation: blur-text 1.5s 1.2s infinite linear alternate;
}

@-webkit-keyframes blur-text {
	0% {
		-webkit-filter: blur(0);
		        filter: blur(0);
	}

	100% {
		-webkit-filter: blur(4px);
		        filter: blur(4px);
	}
}

@keyframes blur-text {
	0% {
		-webkit-filter: blur(0);
		        filter: blur(0);
	}

	100% {
		-webkit-filter: blur(4px);
		        filter: blur(4px);
	}
}

.container {
	display: grid;
	grid-template-columns: 400px 1fr 150px;

	min-height: calc(100vh - 70px - 64px);
}

.sidebar {
	display: grid;
	justify-content: center;

	color: var(--white);
}

.menu-list {
	display: grid;

	margin-top: 45px;
	grid-gap: 25px;
	gap: 25px;
}

.menu-list li {
	padding-left: 15px;

	list-style-type: none;

	cursor: pointer;
}

.menu-list li:hover {
	transition: all 0.3s;

	color: rgba(220, 223, 6, 1);
}

.search-btn {
	height: 45px;

	margin-left: 5px;
	padding: 0 15px;

	cursor: pointer;
	transition: all 0.4s;

	border-style: none;

	border-radius: 5px;
	background-color: var(--white);
}

.search-btn:hover {
	background-color: rgba(220, 223, 6, 1);
}

.filter {
	display: flex;
	grid-gap: 2rem;
	gap: 2rem;
}

.input-field input {
	box-sizing: border-box;
	width: 200px;
	height: 45px;
	padding: 0 10px 0 15px;

	border-style: none;
	border-radius: 5px;
	box-shadow: 0 0 10px rgba(126, 125, 125, 0.6);

	font-family:
		-apple-system,
		BlinkMacSystemFont,
		'Montserrat',
		'Segoe UI',
		'Roboto',
		'Oxygen',
		'Ubuntu',
		'Cantarell',
		'Fira Sans',
		'Droid Sans',
		'Helvetica Neue',
		sans-serif;
}

.input-field input::-webkit-input-placeholder {
	font-family:
		-apple-system,
		BlinkMacSystemFont,
		'Montserrat',
		'Segoe UI',
		'Roboto',
		'Oxygen',
		'Ubuntu',
		'Cantarell',
		'Fira Sans',
		'Droid Sans',
		'Helvetica Neue',
		sans-serif;
}

.input-field input:-ms-input-placeholder {
	font-family:
		-apple-system,
		BlinkMacSystemFont,
		'Montserrat',
		'Segoe UI',
		'Roboto',
		'Oxygen',
		'Ubuntu',
		'Cantarell',
		'Fira Sans',
		'Droid Sans',
		'Helvetica Neue',
		sans-serif;
}

.input-field input::placeholder {
	font-family:
		-apple-system,
		BlinkMacSystemFont,
		'Montserrat',
		'Segoe UI',
		'Roboto',
		'Oxygen',
		'Ubuntu',
		'Cantarell',
		'Fira Sans',
		'Droid Sans',
		'Helvetica Neue',
		sans-serif;
}

.input-field button {
	font-family:
		-apple-system,
		BlinkMacSystemFont,
		'Montserrat',
		'Segoe UI',
		'Roboto',
		'Oxygen',
		'Ubuntu',
		'Cantarell',
		'Fira Sans',
		'Droid Sans',
		'Helvetica Neue',
		sans-serif;
}

.sidebar.back {
	width: 50%;
	margin: 0 auto;
	padding: 4px;

	cursor: pointer;

	-webkit-user-select: none;

	    -ms-user-select: none;

	        user-select: none;

	border: 1.5px solid yellow;
	border-radius: 5px;
	box-shadow: 0 0 6px rgba(220, 223, 6, 0.8);
}

.sidebar.back:hover {
	transition: all 0.5s;

	background: rgba(220, 223, 6, 0.1);
}

.card-movie {
	display: grid;
	grid-template-columns: auto 1fr;

	grid-gap: 50px;

	gap: 50px;
}

.card-movie img {
	width: 360px;

	border-radius: 10px;
	box-shadow: 0 0 10px rgba(220, 223, 6, 1);
}

.card-movie__body {
	display: flex;
	flex-direction: column;
	grid-gap: 15px;
	gap: 15px;
}

.card-movie__slogan {
	color: yellow;
}

.tag {
	margin-left: 10px;
	padding: 4px;

	-webkit-user-select: none;

	    -ms-user-select: none;

	        user-select: none;

	border: 1.5px solid yellow;
	border-radius: 5px;
	box-shadow: 0 0 10px rgb(220 223 6);
}

